.zard {
  position: relative;
}
.zard img {
  width: 100%;
  display: block;
  border: 2px solid #65a1fa;
  border-radius: 6px;
}

/* front of zard - the picture */
.zard .front {
  transform: rotateY(90deg);
  transition: all ease-in 0.2s;
  position: absolute;
}
.flipped .front {
  transform: rotateY(0deg);
  transition-delay: 0.2s;
}

/* back of zard - cover */
.zard .back {
  transition: all ease-in 0.2s;
  transition-delay: 0.2s;
}
.flipped .back {
  transform: rotateY(90deg);
  transition-delay: 0s;
}
