.bg_lolr {
  background: -webkit-linear-gradient(right, #ffa4a4, #ff0000);
  background: linear-gradient(to right, #ffa4a4, #ff0000);
  /* background: linear-gradient(to right, #e86b6b, #ffa4a4); */
}

.bg_lolg {
  background: -webkit-linear-gradient(right, #67f767c0, #00ba00);
  /* background: linear-gradient(to right, #67f767c0, #00ba00); */
  background: linear-gradient(to right, #67f767, #00ba00);
}

.bg_lolp {
  background: -webkit-linear-gradient(right, #ae83fdc9, #6f42c1);
  background: linear-gradient(to right, #ae83fdc9, #6f42c1);
}
.bg_lolb {
  background: -webkit-linear-gradient(right, #83c6fdc9, #0d6efd);
  background: linear-gradient(to right, #83d0fdc9, #0d6efd);
}

.table-fixed {
  width: 100%;
  /* background-color: #3b3535; */
}
.table-fixed tbody {
  height: 200px;
  overflow-y: auto;
  width: 100%;
}
.table-fixed thead,
.table-fixed tbody,
.table-fixed tr,
.table-fixed td,
.table-fixed th {
  display: block;
}
.table-fixed tbody td {
  float: left;
}
.table-fixed thead tr th {
  float: left;
  background-color: rgba(255, 255, 255, 0.152);
  /* background-color: #f39c12;
  border-color: #e67e22; */
}

::-webkit-scrollbar {
  width: 6px;
}
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 20px;
}
::-webkit-scrollbar-thumb {
  /* box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); */
  /* background-image: linear-gradient(45deg, #00dbde, #fc00ff); */
  background-color: rgba(0, 0, 0, 0.25);
  border-radius: 20px;
}
